.post__del-button {
	display: none;
	background: url(../../../../images/delete-button.svg) no-repeat center;
	width: 18px;
	height: 19px;
	border: none;
	position: absolute;
	right: 15px;
	top: 18px;
	cursor: pointer;
	transition: opacity .3s;
}
.post__del-button:hover {
	opacity: .6;
}